import chooseImg from './editGood-ChooseImg.vue'
export default {
    components: {
        chooseImg
    },
    props: {
        label: {
            type: String,
            default: '标签'
        },
        tips: {
            type: String,
            default: ''
        },
        maxNum: {
            type: Number,
            default: 1
        },
        button: {
            type: String,
            default: '照片'
        },
        imglist: {
            label: '主图',
            id: '',
            img: '/static/img/global/home_order_tobepay.png',
            list: [{
                img: '/static/img/global/home_order_tobepay.png',
            }],
            disabled: false, //可否编辑
            editable: 'image', //如何编辑，input当前页输入，switch当前页选择，image选图，imagelist图列，select跳转
        },
        other: {
            type: [Object, Number, String, Array, Boolean],
            default: ''
        }
    },
    data() {
        return {
            dataName: 'lksdjfk'
        }
    },
    methods: {
        selectCell() {
            this.$emit('click', {
                label: this.label,
                value: this.value
            })
        },
        getImages(list) {
            this.$emit('getImages', {
                images: list,
                label: this.label,
                maxNum: this.maxNum,
                button: this.button,
                tips: this.tips,
                other:this.other
            })
        },
        startmove(bool) {
            this.$emit('startmove', bool)
        }
    },
}
